<template>

    <div class="container center-align" id="latest_recipe">
        <h6 class="left-align" >{{title}}</h6>
        <div class="row">

            <md-card md-with-hover class="col l4 s8" v-for="(recipe,index) in recipes" :key="index" >
                <md-card-area>
                    <md-card-media>
                    <img :src="recipe.img" alt="image here">
                    </md-card-media>

                    <md-card-actions>
                    <md-button class="md-icon-button">
                        <md-icon>favorite</md-icon>
                    </md-button>

                    <md-button class="md-icon-button">
                        <md-icon>bookmark</md-icon>
                    </md-button>

                    <md-button class="md-icon-button">
                        <md-icon>share</md-icon>
                    </md-button>
                    </md-card-actions>


                    <md-card-content>{{kontoru}}</md-card-content>

                </md-card-area>
            </md-card>

        </div>
    </div>

</template>

<script>

const description = "酒の提供の有無にかかわらず、\
                    すべてのカラオケ店に対し、\
                    3日から今月末まで営業時間を午後10時まで \
                    に短縮するよう要請します。"

export default {
    data (){
        return {
            kontoru : description ,
            title : "最新のレシピ",
            recipes : [
                {
                    "name" : "unagi",
                    "img" : "img/unagi.jpg",
                    "desc" : description
                },
                {
                    "name" : "sushi",
                    "img" : "img/sushi.jpg",
                    "desc" : description
                },
            ]
    
        }
    }
}



</script>